.tooltip {
  position: absolute;
  display: inline-block;
}
.tooltip p {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  visibility: hidden;
  opacity: 0;
  bottom: -160px;
  left: -280px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  padding: .5em 1em;
  border-radius: 3px;
  background-color: #00000099;
  color: #fff;
  font-size: 1em;
  white-space: nowrap;
  -webkit-transition: opacity .4s;
  transition: opacity .4s;
  text-align: left;
  z-index: 99999;
  pointer-events: none; /* ツールチップ自体をタップされてしまうケースを防ぐためタップ無効化 */
}
.tooltip:hover>p {
  position: absolute;
  visibility: visible;
  opacity: 1;
  -webkit-transition-delay: 0.7s;
          transition-delay: 0.7s;
}
.tooltip p::before {
  position: absolute;
  top: 0px;
  width: 0px;
  height: 0px;
  background-color: inherit;
  -webkit-clip-path: polygon(10% 0, 0 100%, 100% 100%);
          clip-path: polygon(10% 0, 0 100%, 100% 100%);
  content: '';
  z-index: 99999;
}
